html {
  font-size: 62.5%; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.txt-pink {
  color: #FF98E5; }

.body {
  font-family: 'Dela Gothic One', 'Arial', sans-serif;
  min-height: 100vh;
  background-color: #242731;
  color: #ffffff; }

.header {
  padding-top: 5.6rem;
  padding-bottom: 9.3rem;
  overflow: hidden;
  position: relative; }
  .header svg {
    position: absolute;
    top: -150px;
    left: 50%;
    transform: translateX(-50%); }

.header__wrapper {
  width: 100%;
  max-width: 144rem;
  margin: 0 auto;
  max-width: 150rem; }

.navigation {
  position: relative;
  z-index: 100; }

.navigation__list {
  display: flex;
  justify-content: center;
  list-style: none; }

.navigation__item {
  margin-left: 3rem;
  margin-right: 3rem; }
  .navigation__item:first-child {
    margin-left: 0; }
  .navigation__item:last-child {
    margin-right: 0; }

.navigation__link {
  font-size: 3.6rem;
  color: #ffffff;
  opacity: 0.4;
  transition: opacity 0.3s;
  text-decoration: none;
  font-size: 36px; }
  .navigation__link--active {
    opacity: 1; }
  .navigation__link:hover, .navigation__link:focus {
    opacity: 1;
    outline: none; }
